<template>
    <div>
        <el-row>
            <el-col :span="2.7"
                    :key="tag"
                    v-for="(tag,i) in TagData">
                <el-tag
                        size="big"
                        v-show="!changeVisible[i]"
                        closable
                        @click="changeInput(tag)"
                        @close="handleClose(tag)">
                    {{tag}}
                </el-tag>
                <el-input
                        @blur="handleChangeConfirm(i)"
                        class="input-new-tag"
                        size="small"
                        ref="changeTagInput"
                        v-model="changeValue"
                        v-show="changeVisible[i]">
                </el-input>
            </el-col>
            <el-input
                    class="input-new-tag"
                    v-if="inputVisible"
                    v-model="inputValue"
                    ref="saveTagInput"
                    size="small"
                    @blur="handleInputConfirm">
            </el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New</el-button>
        </el-row>

    </div>
</template>

<script>
    export default {
        name: "TagItem",
        data() {
            return {
                TagData: this.Data,
                inputVisible: false,
                changeValue: '',
                inputValue: '',
                index: this.Index,
                changeVisible: [false, false, false, false, false, false, false, false, false, false, false, false, false, false, false, false]
            }
        },
        props: ["Data", "Index"],//接收来自父组件的数据
        watch: {
            Data(data) {
                this.TagData = data; //每次Props数据改变的时候同步更新到TagData
            },
            Index(index) {
                this.index = index;
            }
        },

        methods: {

            handleClose(tag) {
                let indexOfTag = this.TagData.indexOf(tag)
                this.TagData.splice(indexOfTag, 1);
                this.$emit('handleClose', indexOfTag, this.index)
            },

            showInput() {
                this.inputVisible = true;
                this.$nextTick(() => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },

            handleInputConfirm() {
                let inputValue = this.inputValue;
                if (inputValue) {
                    this.TagData.push(inputValue);
                }
                this.inputVisible = false
                this.inputValue = ''
                this.$emit('GetData', this.TagData, this.index)
            },
            handleChangeConfirm(index) {
                this.$set(this.changeVisible, index, false)
                this.$set(this.TagData, index, this.changeValue)
                this.changeValue=''
                this.$emit('GetData', this.TagData, this.index)
            },
            changeInput(tag) {
                let index = this.TagData.indexOf(tag);
                this.changeValue = tag
                this.$set(this.changeVisible, index, true)
                this.$nextTick(() => {
                    this.$refs.changeTagInput[index].$refs.input.focus();
                });
            },
        }
    }
</script>

<style scoped>
    .el-tag + .el-tag {
        margin-left: 10px;
    }

    .button-new-tag {
        /*margin-left: 10px;*/
        /*height: 32px;*/
        /*line-height: 30px;*/
        /*padding-top: 0;*/
        /*padding-bottom: 0;*/
    }

    .input-new-tag {
        padding: 0;
        width: 70px;
        /*vertical-align: bottom;*/
    }
</style>