<template>
    <div class="contain_box">
        <div class="ruleForm_box">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm center"
                     v-show="!isSubmit">
                <el-form-item label="请输入cycleLengthThreshold:  (20-100)" class="input_box">
                    <el-input v-model="ruleForm.cycleLengthThreshold" autocomplete="off"
                              prefix-icon="el-icon-caret-right"
                              placeholder="20-100" size="small"
                              class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="请输入dustLengthThreshold:  (8-10)" class="input_box">
                    <el-input v-model="ruleForm.dustLengthThreshold"
                              autocomplete="off" placeholder="8-10" size="small"
                              prefix-icon="el-icon-caret-right"
                              class="el-input"></el-input>
                </el-form-item>
                <el-form-item label="请输入物种数:" class="tag_content">

                    <!--                <el-input v-model="ruleForm.countNum" autocomplete="off" placeholder="22-11-7"></el-input>-->
                </el-form-item>
                <TagItem :Data="ruleForm.countNum" :index="1"
                         @handleClose="handleClose" @GetData="GetData" class="tag_contain"></TagItem>
                <el-form-item label="请输入物种名:" class="tag_content">
                    <!--                <el-input v-model="ruleForm.animalName" autocomplete="off" placeholder="人类-熊猫-大狮子"></el-input>-->
                </el-form-item>
                <TagItem :Data="ruleForm.animalName" :index="0"
                         @handleClose="handleClose" @GetData="GetData" class="tag_contain"></TagItem>

                    <el-upload
                            class="up_load_box"
                            action="/upload"
                            :limit="1"
                            :file-list="fileList"
                            :on-exceed="handleExceed"
                            :on-remove="handleRemove"
                            :auto-upload="true"
                            :before-upload="handlePreview"
                            :on-success="handleSuccess">
                        <el-button class="uploadButton">上传文件</el-button>
                    </el-upload>

                <el-form-item>
                    <el-button type="primary" @click="uploadParam()" class="submitButton">提交</el-button>
                    <el-button type="primary" @click="backToMain()" class="submitButton">返回首页</el-button>
                </el-form-item>
            </el-form>
        </div>

    </div>
</template>

<script>
    import axios from 'axios';
    import TagItem from '../components/Tag'

    export default {
        components: {
            TagItem
        },
        name: "submit",
        mounted() {
            if (this.$route.params.data !== undefined) {
                this.ruleForm.id = this.$route.params.data.id
                this.fileList = [{
                    name: this.$route.params.data.oriName,
                    url: ""
                }]
            }
        },
        methods: {
            handleClose(indexOfTag, index) {
                if (index === 1) {
                    this.ruleForm.animalName.splice(indexOfTag, 1);
                } else if (index === 0) {
                    this.ruleForm.countNum.splice(indexOfTag, 1);
                }
            },
            handleRemove() {
                axios.get('/delete', {
                    params: {
                        id: this.ruleForm.id,
                    }
                })
            },
            handleExceed() {
                this.$message.warning(`当前限制选择 1 个文件`);
            },
            handleSuccess(response) {
                this.ruleForm.id = response.data
            },
            handlePreview(file) {
                console.log(file.name);
                let fileList = file.name.split('.')
                if (fileList[1] !== 'sequence') {
                    this.putMessage("请上传sequence文件")
                    return false;
                }
            },
            backToMain() {
                this.$router.push({
                    name: 'mainView', params: {}
                })
            },
            putMessage: function (meg) {
                this.$message({
                    message: meg,
                    duration: 3000,
                    showClose: true,
                    type: 'warning'
                })
            },
            uploadParam() {
                if (this.ruleForm.id.length === 0) {
                    this.putMessage('请上传文件!')
                    return;
                }
                if (this.ruleForm.dustLengthThreshold.length === 0) {
                    this.putMessage('请输入参数!')
                    return;
                }
                if (this.ruleForm.dustLengthThreshold > 10 || this.ruleForm.dustLengthThreshold < 8) {
                    this.putMessage("dustLength不正确");
                    return;
                }
                if (this.ruleForm.cycleLengthThreshold.length === 0) {
                    this.putMessage("请输入参数");
                    return;
                }
                if (this.ruleForm.cycleLengthThreshold > 100 || this.ruleForm.cycleLengthThreshold < 10) {
                    this.putMessage("cycleLengthThreshold不正确");
                    return;
                }
                if (this.ruleForm.countNum.length !== this.ruleForm.animalName.length) {
                    this.putMessage('输入个数不正确')
                    return;
                }

                this.isSubmit = true;
                let countNum = ""
                let animalName = ""
                for (let i = 0; i < this.ruleForm.countNum.length; i++) {
                    countNum += this.ruleForm.countNum[i];
                    if (i !== this.ruleForm.countNum.length - 1) {
                        countNum += '-'
                    }
                }
                for (let i = 0; i < this.ruleForm.animalName.length; i++) {
                    animalName += this.ruleForm.animalName[i];
                    if (i !== this.ruleForm.animalName.length - 1) {
                        animalName += '-'
                    }
                }

                axios.get('/submit', {
                    params: {
                        id: this.ruleForm.id,
                        cycleLengthThreshold: this.ruleForm.cycleLengthThreshold,
                        dustLengthThreshold: this.ruleForm.dustLengthThreshold,
                        countNum: countNum,
                        animalName: animalName
                    }
                }).then((response) => {
                        if (response.data.code === 20000) {

                            clearTimeout(this.timer);  //清除延迟执行
                            this.timer = setTimeout(() => {   //设置延迟执行
                                this.$router.push({
                                    name: 'mainView'
                                })
                            }, 1000)
                        } else {
                            this.putMessage(response.data.message);
                            this.isSubmit = false;
                        }
                        axios.get('/calculate', {
                            params: {
                                id: this.ruleForm.id,
                                cycleLengthThreshold: this.ruleForm.cycleLengthThreshold,
                                dustLengthThreshold: this.ruleForm.dustLengthThreshold,
                                countNum: countNum,
                                animalName: animalName
                            }
                        }).then(() => {
                            this.$bus.emit('Config_forms', "1")
                            this.isSubmit = false;

                        });
                    }
                )


            },
            GetData(data, index) { //这里的data是接收子组件传递过来的值
                if (index === 1) {
                    this.ruleForm.countNum = data; //把子组件的数据同步到父组件中
                } else {
                    this.ruleForm.animalName = data; //把子组件的数据同步到父组件中
                }
            },
        },
        data() {
            return {
                fileList: [],
                TagData: [],
                isSubmit: false,
                ruleForm: {
                    cycleLengthThreshold: '20',
                    dustLengthThreshold: '8',
                    countNum: ['22', '11', '7'],
                    animalName: ['人类', '熊猫', '狮子'],
                    id: '',
                },
            }
        }
    }
</script>

<style scoped>
    .up_load_box {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .tag_contain {
        text-align: left;
    }

    .tag_content {
        width: 500px;
        text-align: left;
        margin-bottom: 0px;
    }

    .uploadButton {
        width: 500px;
        height: 30px;
        text-align: center;
        font-size: 10px;
    }

    .input_box {
        width: 500px;
        margin: 0 auto;
        border-radius: 20px;
    }

    .el-input {

        border-radius: 30px;
    }

    .demo-ruleForm {
        width: 500px;
        margin-bottom: 0;
    }

    .ruleForm_box {
        padding-top: 30px;
        padding-bottom: 20px;
    }

    .submitButton {
        margin-top: 20px;
    }

    .contain_box {
        width: 1000px;
        border-radius: 10px;
        background: #ddefefdd;
    }

    .center {
        margin: 0 auto;
    }
</style>